$width-form: 400px;
$width-label: 100px;
#login {
  height: 100%;
  width: 100%;
  background: url('../../assets/images/background.png') no-repeat center center rgba(0,0,0,1);
  // background-color: $background-color;
  .form {
    width: $width-form;
    margin: auto;
    padding-top: 200px;
    .title {
      width: 100%;
      margin-bottom: 20px;
      text-align: center;
      image {
        width: 80%;
      }
    }
    .item {
      border: 1px solid rgba(255, 255, 255, 0.1);
      background: rgba(0, 0, 0, 0.1);
      .el-form-item__label::before {
        content: "";
      }
      .el-form-item__label {
        text-align: left;
        padding-left: 20px;
        color: #9da0aa;
      }
      input {
        background: transparent;
        border: 0;
        -webkit-appearance: none;
        border-radius: 0;
        color: #eee;
        caret-color: #fff;
      }
      .el-form-item__error {
        left: -$width-label;
      }
      .display {
        position: absolute;
        right: 10px;
        color: #c0c3cc;
        cursor: pointer;
      }
    }
    .submit {
      margin-top: 5px;
      .el-form-item__content {
        margin-left: 0 !important;
      }
      .btn {
        &.btn-login {
          width: 100%;
          // color: #606266;
        }
      }
    }
  }
}
